import React from "react"
// import { Link } from "gatsby"
import {graphql, Link} from 'gatsby'
import Layout from "../components/layout/layout"
import { Parallax } from 'react-parallax';
import '../components/layout/index.scss';
import SliderView from '../components/SliderView';
import background from '../../content/images/pattern.png';
import divider from '../../content/images/home/header_divider.png'
import Instafeed from 'react-instafeed';
import { Container } from 'react-bootstrap';

const IndexPage = ({data}) => (
    <Layout title="Home">
      <SliderView images={data}/>
      <Parallax bgImage={background} strength={300}>
        <div className="para-content">
          <span className="title">When children's hands are moving,</span>
          <span className="title">their minds are learning!</span>
        </div>
      </Parallax>
      <div className="main-content container">
        <h1>Come sign with Expressive Hands!</h1>
        <div className="divider" style={{backgroundImage: `url(${divider})`}}></div>
        <p>
           Learn sign language together with your child. We teach beginner American Sign Language
          (ASL) vocabulary, based on the award-winning <a href="www.signingtime.com" target="_blank">Baby Signing Time Program</a> to children ages 0-5 and their families.
        </p>
        <p>
          Expressive Hands, LLC offers Sign+Play Classes, Story Times, Parent Workshops and Educational Products as a proud member of the Signing Time Academy! Click <Link to="/">here</Link> for class information.
        </p>
        <p>Join our <Link to="/contact">mailing list</Link> to stay up to date with the latest news from Expressive Hands! </p>
        <iframe width="560" height="315" src="https://www.youtube.com/embed/pYdLsH4h8LM" frameBorder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen></iframe>
        <br/>
        <h4>Ready - Set - Sign!</h4>
        <p><i>It's never too EARLY or too LATE to start signing with your little one!</i></p>
      </div>
      <Container className="instagram-feed">
        <h4>Check out our latests posts on Instagram!</h4>
        <div id="instafeed">
          <Instafeed
              userId="11367482566"
              limit="6"
              resolution="thumbnail"
              sortBy="most-recent"
              target="instafeed"
              template={`<div class="item col-6 col-sm-4 col-md-2"><figure className="overlay overlay3"><a href="{{link}}" target="_blank"><span className="bg"></span><img src="{{image}}" /><figcaption className="d-flex"><div className="align-self-center mx-auto"><i className="fa fa-instagram"></i></div></figcaption></figure></div>`}
              accessToken="11367482566.663e968.6c96ed8c472f4c0bac66a35aaf2aa5cf"
          />
        </div>
      </Container>
    </Layout>
);

export const query = graphql`
  query {
    slider: allFile(
      filter: {
        extension: { regex: "/(jpeg|jpg|gif|png)/" }
        dir: { regex: "/home/slider/" }
        sourceInstanceName: { eq: "images" }
      },
      sort: { fields: [name], order: ASC}
      ) {
      images: edges {
        img: node {
          childImageSharp {
            fluid(maxWidth: 2000, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default IndexPage
